.poster {
  width: 300px;
  aspect-ratio: 9/12;
  border-radius: 14px;
  background-position: center;
  background-size: cover;
  display: flex;
  padding: 4px 10px;
  cursor: pointer;
}

.poster > p {
  align-self: flex-end;
  font-size: 15px;
  font-weight: 700;
  color: white;
}
